@import "https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap";

/* src/app/pages/home/productos/productos.scss */
:root {
  --clr-blue:#05668D;
  --clr-ligth_blue:#427AA1;
  --clr-white:#EBF2FA;
  --clr-green:#679436;
  --clr-ligth_green:#A5BE00;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Ubuntu", sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
input,
textarea,
ul {
  margin: 0;
  padding: 0;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
main {
  background-color: var(--clr-white);
  margin: 1rem;
  margin-left: 0;
  border-radius: 2rem;
  padding: 2rem;
}
.titulo-principal {
  color: var(--clr-green);
  margin-bottom: 2rem;
}
.contenedor-productos {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
}
.producto-imagen {
  max-width: 100%;
  border-radius: 1rem;
}
.producto-detalle {
  background-color: var(--clr-blue);
  color: var(--clr-white);
  padding: 0.25rem;
  border-radius: 1rem;
  margin-top: -2rem;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
.producto-titulo {
  font-size: 1rem;
}
.producto-agregar {
  border: 0;
  background-color: var(--clr-white);
  color: var(--clr-green);
  padding: 0.25rem;
  text-transform: uppercase;
  border-radius: 2rem;
  cursor: pointer;
  border: 2px solir var(--clr-white);
  transition: background-color 0.2s, color 0.2s;
}
.producto-agregar:hover {
  background-color: var(--clr-green);
  color: var(--clr-white);
}
.menu {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.cant {
  background-color: var(--clr-white);
  color: var(--clr-green);
  padding: 0.15rem 0.25rem;
  border-radius: 0.25rem;
}
/*# sourceMappingURL=productos.css.map */
